import './App.css';
import '@tremor/react/dist/esm/tremor.css';
import { useState } from 'react';
import {
    Block,
    Tab,
    TabList,
    Text,
    Title,
} from '@tremor/react';

import TopMetrics from './components/TopMetrics';
import TopMetricsCompare from './components/TopMetricsCompare';
import ChartView from './components/ChartView';
import ChartViewCompare from './components/ChartViewCompare';
import FeatureOverview from './components/FeatureOverview';


function ModelPerformance() {
    return <>
        <TopMetrics />
        
        <Block marginTop="mt-6">
            <ChartView />
        </Block>
        
        
    </>
}

function ModelComparison() {
    return <>
        <TopMetricsCompare />
        
        <Block marginTop="mt-6">
            <ChartViewCompare />
        </Block>
    </>
}

function Features() {
    return <>
        <Block marginTop="mt-6">
            <FeatureOverview />
        </Block>
    </>
}

function Tabs(selectedView) {
    if(selectedView === 1)
        return ModelPerformance()
    if(selectedView === 2)
        return Features()
    if(selectedView === 3)
        return Features()
    if (selectedView === 4)
        return ModelComparison()
}


function App() {
  const [selectedView, setSelectedView] = useState(1);
    return (
        <main className="bg-slate-50 p-6 sm:p-10">
            <Title>Model Performance</Title>
            <Text>
                Lorem ipsum dolor sit amet, consetetur sadipscing elitr.
            </Text>

            <TabList defaultValue={ 1 } handleSelect={ (value) => setSelectedView(value) } marginTop="mt-6">
                <Tab value={ 1 } text="Model Performance" />
                <Tab value={ 2 } text="Feature Overview" />
                <Tab value={ 3 } text="Target Overview" />
                <Tab value={ 4 } text="Compare Models" />
            </TabList>

            {Tabs(selectedView)}

            {/* { selectedView === 1 ? (
                <>
                    <TopMetrics />
                    
                    <Block marginTop="mt-6">
                        <ChartView />
                    </Block>
                    
                    
                </>
            ) : (
                <>
                    <TopMetricsCompare />
                    
                    <Block marginTop="mt-6">
                        <ChartViewCompare />
                    </Block>
                </>
            ) } */}
        </main>
    );
}

export default App;
