import { useState } from 'react';
import {
    AreaChart,
    Block,
    Card,
    Flex,
    ColGrid,
    Text,
    Title,
    Toggle,
    ToggleItem,
    SelectBox, 
    SelectBoxItem
} from '@tremor/react';
// import { InformationCircleIcon } from '@heroicons/react/outline';

export const performance = [
    {
        date: '2021-01-01', Sales: 900.73, Profit: 173, Customers: 73,
    },
    {
        date: '2021-01-02', Sales: 1000.74, Profit: 174.6, Customers: 74,
    },
    // ...
    {
        date: '2021-03-13', Sales: 882, Profit: 682, Customers: 682,
    },
];

// Basic formatters for the chart values
const dollarFormatter = (
    value,
) => (`$ ${Intl.NumberFormat('us').format(value).toString()}`);

const numberFormatter = (
    value,
) => (`${Intl.NumberFormat('us').format(value).toString()}`);

export default function ChartViewCompare() {
    const [selectedKpi, setSelectedKpi] = useState('Sales');

    // map formatters by selectedKpi
    const formatters = {
        Sales: dollarFormatter,
        Profit: dollarFormatter,
        Customers: numberFormatter,
    };

    return (
        <Card>
            <div className="md:flex justify-between">
                <Block>
                    <Flex justifyContent="justify-start" spaceX="space-x-0.5" alignItems="items-center">
                        <Title> Performance Details </Title>
                        {/* <Icon
                            icon={ InformationCircleIcon }
                            variant="simple"
                            tooltip="Shows day-over-day (%) changes of past performance"
                        /> */}
                    </Flex>
                    <Text> Daily increase or decrease per domain </Text>
                </Block>
                <div className="mt-6 mr-3 md:mt-0">
                    <SelectBox
                        defaultValue={undefined}
                        value={undefined}
                        onValueChange={undefined}
                        placeholder="Cutoff..."
                        icon={undefined}
                        maxWidth="max-w-none"
                        marginTop="mt-0"
                    >
                    <SelectBoxItem
                        value={undefined} 
                        text="" 
                        icon={undefined} />
                    </SelectBox>
                </div>


                <div className="mt-6 md:mt-0">
                    <Toggle
                        color="zinc"
                        defaultValue={ selectedKpi }
                        handleSelect={ (value) => setSelectedKpi(value) }
                    >
                        <ToggleItem value="Sales" text="Confusion Matrix" />
                        <ToggleItem value="Profit" text="AUC-ROC Curve" />
                        <ToggleItem value="Customers" text="AUC-PR Curve" />
                    </Toggle>
                </div>
            </div>
            
            <ColGrid numColsSm={ 2 } numColsLg={ 2 } marginTop="mt-6" gapX="gap-x-6" gapY="gap-y-6">
                <AreaChart
                    data={ performance }
                    dataKey="date"
                    categories={ [selectedKpi] }
                    colors={ ['orange'] }
                    showLegend={ false }
                    valueFormatter={ formatters[selectedKpi] }
                    yAxisWidth="w-7"
                    height="h-96"
                    marginTop="mt-8"
                />
                <AreaChart
                    data={ performance }
                    dataKey="date"
                    categories={ [selectedKpi] }
                    colors={ ['yellow'] }
                    showLegend={ false }
                    valueFormatter={ formatters[selectedKpi] }
                    yAxisWidth="w-7"
                    height="h-96"
                    marginTop="mt-8"
                />
                
            </ColGrid>
            

        </Card>
    );
}