export default function TopMetricsCompare() {
    return (
        <ColGrid numColsSm={ 2 } numColsLg={ 2 } marginTop="mt-6" gapX="gap-x-6" gapY="gap-y-6">
            { categories.map((item) => (
                <Card key={ item.title } decoration="top" decorationColor={ item.color }>
                    <Text>{ item.title }</Text>
                    <Metric>{ item.metric }</Metric>
                    <List marginTop="mt-4">
                        { item.data.map((product) => (
                            <ListItem key={ product.name }>
                                <Block>
                                    <Text>
                                        { product.name }
                                    </Text>
                                    <ProgressBar
                                        percentageValue={ product.share }
                                        label={ `${product.share}%` }
                                        tooltip={ product.amount }
                                    />
                                </Block>
                            </ListItem>
                        )) }
                    </List>
                </Card>
            )) }
        </ColGrid>
    );
}